<template>
  <li @click="$emit('changeCurrent',index)">
    <router-link :to="{path:'/gists/gist', query:{id:item.id}}">
      <span class="item-desc">{{item.description}} </span>
    </router-link>
    <span class="item-delete" @click="deleteCurrentGist(item.id)"><i class="el-icon-delete"></i></span>
  </li>
</template>

<script>
  import bus from "./../../bus.js"
  import http from "axios"

  export default {
    name: "ListItem",
    props:["item","index"],
    mounted(){
        bus.$on("defaultTab",(id) => {
            console.log(id);
        });
    },
    methods:{
      deleteCurrentGist(id){
        http.delete("https://api.github.com/gists/"+ id,{
          headers: {
            "Authorization": "token " + localStorage.getItem("token")
          }
        })
          .then((r) => {
            console.log(r);
            bus.$emit("deletePageGist",id);
          })
          .catch();
      }
    }
  }
</script>

<style scoped>
  .item-desc{
    width:90%;
    display: inline-block;
  }
  .item-delete {
    color: #f00;
    font-size:12px;
    cursor: pointer;
  }
</style>



